<script>
  import axios from 'axios'
  import $ from 'jquery'

  var globallanguage = 'es'
  if (globaldata.language !== null && globaldata.language !== '') {
    globallanguage = globaldata.language
  }
  const langfile = require('../../assets/lang/content.json')

  var highstart = new Date('2019', '04', '13')
  var highend = new Date('2019', '04', '17')
  var today = new Date()
  var tomorrow = new Date()
  tomorrow.setDate(tomorrow.getDate() + 1)

  if (today < highstart) {
    console.log('entra')

    today = highstart
    tomorrow = highend

    console.log('tomorrow' + tomorrow)
    console.log('today' + today)
  }
  export default {
    name: 'Formulario',
    data: function () {
      return {
        selectiontype: null,
        isairportdisabled: false,
        alldestinations: globaldata.alldestinations,
        isdestdisabled: false,
        contactmail: globaldata.contactmail,
        whitebrandairport: globaldata.whitebrandtransferorigin,
        whitebranddest: globaldata.whitebrandtransferdest,
        airports: null,
        specialtransportactive: false,
        destination: null,
        destino: null,
        iddestino: null,
        origen: null,
        idorigen: null,
        entrada: this.getInitialDate('entrada'),
        salida: this.getInitialDate('salida'),
        fechaentradaclass: {
          class: 'form-control'
        },
        fechasalidaclass: {
          class: 'form-control'
        },
        trayecto: 'roundtrip',
        pax: 1,
        portfolio: null,
        busqueda: null,

        bike: 0,
        golf: 0,
        ski: 0,
        wheelchair: 0,
        bigluggage: 0,
        coincidencias: {
          matches: []
        },
        loader: null,
        language: globallanguage,
        origobject: {
          resourceId: '',
          name: {}

        },
        destobject: {
          resourceId: '',
          name: {}

        },
        multilanguage: langfile.multilanguage,
        errors: [],
        formats: {
          title: 'MMMM YYYY',
          weekdays: 'W',
          navMonths: 'MMM',
          input: ['L', 'YYYY-MM-DD', 'YYYY/MM/DD'], // Only for `v-date-picker`
          dayPopover: 'L', // Only for `v-date-picker`
          data: ['L', 'YYYY-MM-DD', 'YYYY/MM/DD'] // For attribute dates
        }
      }
    },

    methods: {
      elegirdestino: function (selectype) {
        this.$data.selectiontype = selectype
        $(function () {
          $('#modal1').show()
          $('#busqueda').focus()
        })
      },
      getInitialDate: function(dt){
        let maindata = JSON.parse(localStorage.getItem("qtse_maindatastorage"));
        if (dt === 'entrada')
        {    
          if(this.$route.query.entrada){
            return new Date(this.$route.query.entrada)
          }else{
            if(maindata && maindata.startdate ){
                return new Date(maindata.startdate)
            }
            else
            {
              return today;
            }
          }
        }
        if (dt === 'salida'){
            if(this.$route.query.salida){
              return new Date(this.$route.query.salida)
            }else{
              if(maindata && maindata.enddate ){
                return new Date(maindata.enddate)
              }
              else
              {
                return tomorrow;
              }
          }
        }
          
      },
      buscardestino: function (e) {
        var especial = false
        var d = this.$data
        especial |= e.keyCode === 37
        especial |= e.keyCode === 38
        especial |= e.keyCode === 39
        especial |= e.keyCode === 40

        if (!especial) {
          var coins = this.$data.coincidencias

          axios({
            method: 'get',
            url: globaldata.baseurl + '/commons/searchtransferpoints',
            params: {
              language: d.language,
              query: this.$data.busqueda
            }
          })
            .then(function (response) {
              coins.matches = response.data.matches
            })
            .catch(function (error) {
              console.log(error)
            })
        }
      },
      closemodal: function (e) {

        var d = this.$data
        d.busqueda = null
        d.coincidencias = {matches: []}

        $(function () {
          $('#modal1').toggle()
        })
      },
      seleccionardestino: function (name, resourceId) {
        if (this.$data.selectiontype === 'origen') {
          this.$data.origobject.name[this.$data.language] = name
          this.$data.origobject.resourceId = resourceId
        } else {
          this.$data.destobject.name[this.$data.language] = name
          this.$data.destobject.resourceId = resourceId
        }

        this.closemodal()
      },
      airportChanged: function (id) {
        if (id) {
          var d = this.$data

          axios({
            method: 'get',
            url: globaldata.baseurl + '/transfer/airports/' + id + '/destinations'

          })
            .then(function (response) {
              d.destination = response.data.destination
              d.destobject = JSON.parse(JSON.stringify(d.destination[0]))
            })
            .catch(function (error) {
              console.log(error)
            })
        }
      },

      buscar: function (e) {
        e.preventDefault()

        if (this.$data.trayecto === 'inbound') {
          this.$data.salida = null
        }
        if (this.$data.trayecto === 'outbound') {
          this.$data.entrada = null
        }
        if (!this.$data.specialtransportactive) {
          this.$data.bike = 0
          this.$data.golf = 0
          this.$data.ski = 0
          this.$data.wheelchair = 0
          this.$data.bigluggage = 0
        }
        if (this.checkForm()) {
          this.$router.push({

            name: 'traslado_dispo',
            query: {
              destino: this.$data.destobject.resourceId,
              destinoname: this.$data.destobject.name[this.$data.language],
              origen: this.$data.origobject.resourceId,
              origenname: this.$data.origobject.name[this.$data.language],
              entrada: this.$data.entrada ? this.formatDate(this.$data.entrada) : null,
              salida: this.$data.salida ? this.formatDate(this.$data.salida) : null,
              idioma: this.$data.language,
              bike: this.$data.bike,
              golf: this.$data.golf,
              ski: this.$data.ski,
              wheelchair: this.$data.wheelchair,
              bigluggage: this.$data.bigluggage,
              pax: this.$data.pax
            }
          })
        }
      },
      formatDate: function (date) {
        var d = new Date(date)
        var month = '' + (d.getMonth() + 1)
        var day = '' + d.getDate()
        var year = d.getFullYear()

        if (month.length < 2) month = '0' + month
        if (day.length < 2) day = '0' + day

        return [year, month, day].join('-')
      },
      checkForm: function (e) {
        var existsErrors = false
        var d = this.$data
        this.errors = []
        this.cleanErrors()

        if (d.trayecto === 'roundtrip' || d.trayecto === 'outbound') {
          if (this.salida === null || this.salida === '') {
            this.setValidationError('checkoutdates', 'checkoutdates-msg', this.multilanguage.erroremptycheckout[this.language])
            existsErrors = true
          }
        }
        if (this.pax < 1) {
          this.setValidationError('pax', 'pax-msg', this.multilanguage.errornopersonas[this.language])
          existsErrors = true
        }

        if (d.trayecto === 'roundtrip' || d.trayecto === 'inbound') {
          if (this.entrada === null || this.entrada === '') {
            this.setValidationError('checkindates', 'checkindates-msg', this.multilanguage.erroremptycheckin[this.language])
            existsErrors = true

          } else {
            var fsalida = new Date(this.salida)
            var fentrada = new Date(this.entrada)
            var today = new Date()
            fsalida.setHours(0, 0, 0, 0)
            fentrada.setHours(0, 0, 0, 0)
            today.setHours(0, 0, 0, 0)

            if (d.trayecto === 'roundtrip' && (fentrada.getTime() >= fsalida.getTime())) {
              this.setValidationError('checkoutdates', 'checkoutdates-msg', this.multilanguage.errorcheckoutdatetoday[this.language])
              existsErrors = true

            }
            if (fentrada.getTime() < today.getTime()) {
              this.setValidationError('checkindates', 'checkindates-msg', this.multilanguage.errorcheckindatetoday[this.language])
              existsErrors = true

            }
          }
        }

        if (d.alldestinations) {

          if (this.destobject.resourceId === null || this.destobject.resourceId === '') {
            this.setValidationError('alldestino', 'alldestino-msg', this.multilanguage.errordestinationvacia[this.language])
            existsErrors = true

          }
          if (this.origobject.resourceId === null || this.origobject.resourceId === '') {
            this.setValidationError('allorigen', 'allorigen-msg', this.multilanguage.errorairportvacia[this.language])
            existsErrors = true

          }
        } else {

          if (this.destobject.resourceId === null || this.destobject.resourceId === '') {
            this.setValidationError('destino', 'destino-msg', this.multilanguage.errordestinationvacia[this.language])
            existsErrors = true

          }
          if (this.origobject.resourceId === null || this.origobject.resourceId === '') {
            this.setValidationError('origenx', 'origen-msg', this.multilanguage.errorairportvacia[this.language])
            existsErrors = true
          }
        }
        if (existsErrors) {
          return false
        }
        return true
      },
      cleanErrors: function (email) {
        var d = this.$data

        var element

        if (d.alldestinations) {
          element = document.getElementById('allorigen')
          element.setAttribute('class', 'form-control bg-white')
          element = document.getElementById('alldestino')
          // element.setAttribute('aria-invalid', 'false')
          element.setAttribute('class', 'form-control bg-white')

        } else {
          element = document.getElementById('origenx')

          element.setAttribute('class', 'form-control')
          element = document.getElementById('destino')
          element.setAttribute('aria-invalid', 'false')
          element.setAttribute('class', 'form-control bg-white')

        }

        if (d.trayecto === 'roundtrip' || d.trayecto === 'inbound') {
          d.fechaentradaclass.class = 'form-control'
          element = document.getElementById('checkindates')
          element.setAttribute('aria-invalid', 'false')
          element.setAttribute('class', 'form-control')
        }

        if (d.trayecto === 'roundtrip' || d.trayecto === 'outbound') {
          d.fechaentradaclass.class = 'form-control'
          element = document.getElementById('checkoutdates')
          element.setAttribute('aria-invalid', 'false')
          element.setAttribute('class', 'form-control')
        }
        element = document.getElementById('pax')
        element.setAttribute('aria-invalid', 'false')
        element.setAttribute('class', 'form-control')
      },
      setValidationError (field, msgField, msg) {
        var d = this.$data

        if (field === 'checkindates') {
          d.fechaentradaclass.class = 'form-control is-invalid'
        } else if (field === 'checkoutdates') {
          d.fechasalidaclass.class = 'form-control is-invalid'
        }

        var element = document.getElementById(field)
        element.classList.add('is-invalid')
        element.classList.add('form-control')
        element.focus()

        if (msgField !== null && msgField !== ' ') {
          var elementmsg = document.getElementById(msgField)

          elementmsg.innerHTML = msg
        }
      },
      getallairports () {
        var d = this.$data
        var _this = this

        axios({
          method: 'get',
          url: globaldata.baseurl + '/transfer/airports'
        })
          .then(function (response) {
            d.airports = response.data.airports
            if (d.airports) {
              d.origobject = d.airports[0]

              _this.airportChanged(d.origobject.resourceId)
            }
          })
          .catch(function (error) {
            console.log(error)
          })
      },
      getairportfromid (id) {
        var d = this.$data
        var _this = this

        axios({
          method: 'get',
          url: globaldata.baseurl + '/transfer/airport/' + id
        })
          .then(function (response) {
            d.airports = response.data.airports
            d.origobject = d.airports[0]

            if (!d.whitebranddest) {
              _this.airportChanged(d.origobject.resourceId)
            }
          })
          .catch(function (error) {
            console.log(error)
          })
      },
      getairportfromdest () {
        var d = this.$data

        axios({
          method: 'get',
          url: globaldata.baseurl + '/transfer/airportfromdest/' + d.whitebranddest
        })
          .then(function (response) {
            d.airports = response.data.airports
            d.origobject = d.airports[0]
          })
          .catch(function (error) {
            console.log(error)
          })
      },
      getdestination () {
        var d = this.$data

        axios({
          method: 'get',
          url: globaldata.baseurl + '/transfer/destination/' + d.whitebranddest
        })
          .then(function (response) {
            d.destination = response.data.destination
            d.destino = d.destination[0].name[this.$data.language]
            d.iddestino = d.destination[0].resourceId
          })
          .catch(function (error) {
            console.log(error)
          })
      },
      getalldestinations () {
        var d = this.$data
        var _this = this

        axios({
          method: 'get',
          url: globaldata.baseurl + '/transfer/destinations'
        })
          .then(function (response) {
            console.log(response.data)
            d.destination = response.data.destination
            if (d.destination) {
              d.origobject = JSON.parse(JSON.stringify(d.destination[0]))
              d.destobject = JSON.parse(JSON.stringify(d.destination[0]))
            }

            d.origobject.name[d.language] = _this.$route.query.origenname ? _this.$route.query.origenname : ''
            d.origobject.resourceId = _this.$route.query.origen ? _this.$route.query.origen : ''

            d.destobject.name[d.language] = _this.$route.query.destinoname ? _this.$route.query.destinoname : ''
            d.destobject.resourceId = _this.$route.query.destino ? _this.$route.query.destino : ''

          })
          .catch(function (error) {
            console.log(error)
          })
      }

    },
    mounted () {
      // window.scrollTo(0, 0)
      var d = this.$data
      var _this = this

      if (d.alldestinations) {
        _this.getalldestinations()
      } else {
        if (!d.whitebrandairport && !d.whitebranddest) {
          d.isairportdisabled = false
          d.isdestdisabled = false
          _this.getallairports()
        } else {
          if (d.whitebrandairport) {
            d.isairportdisabled = true
            _this.getairportfromid(d.whitebrandairport)
          }
          if (d.whitebranddest) {
            d.isdestdisabled = true
            _this.getdestination()
            d.iddestino = d.whitebranddest
            if (!d.whitebrandairport) {
              _this.getairportfromdest()
            }
          }
        }
      }

      if (_this.$route.query.destinoname && _this.$route.query.destino) {
        d.destobject.resourceId = _this.$route.query.destino
        d.destobject.name[this.$data.language] = _this.$route.query.destinoname
      }
      if (_this.$route.query.origenname && _this.$route.query.origen) {
        d.origobject.resourceId = _this.$route.query.origen
        d.origobject.name[this.$data.language] = _this.$route.query.origenname
      }

      if (_this.$route.query.pax) {
        d.pax = _this.$route.query.pax
      }
      d.specialtransportactive = false

      if (_this.$route.query.bike && _this.$route.query.bike > 0) {
        d.bike = _this.$route.query.bike
        d.specialtransportactive = true
      }
      if (_this.$route.query.golf && _this.$route.query.golf > 0) {
        d.golf = _this.$route.query.golf
        d.specialtransportactive = true
      }
      if (_this.$route.query.ski && _this.$route.query.ski > 0) {
        d.ski = _this.$route.query.ski
        d.specialtransportactive = true
      }
      if (_this.$route.query.bigluggage && _this.$route.query.bigluggage > 0) {
        d.bigluggage = _this.$route.query.bigluggage
        d.specialtransportactive = true
      }
      if (_this.$route.query.wheelchair && _this.$route.query.wheelchair > 0) {
        d.wheelchair = _this.$route.query.wheelchair
        d.specialtransportactive = true
      }
    },
    updated () {
      if (this.$data.errors.length > 0) {
        document.getElementById('validation-errors').scrollIntoView()
      }
    }
  }
</script>

