<script>
import axios from 'axios'

import noUiSlider from 'nouislider'
import wNumb from 'wnumb'
import 'nouislider/distribute/nouislider.css'


var slider
var globallanguage = 'es'
if (globaldata.language !== null && globaldata.language !== '') {
  globallanguage = globaldata.language
}
const langfile = require('../../assets/lang/content.json')
const noimage = require('../../assets/noimage.jpg')
const shuttleimage = require('../../assets/shuttle.jpg')
const privateimage = require('../../assets/private.jpg')
const executiveimage = require('../../assets/luxury.jpg')
export default {
  name: 'TrasladoDispo',
  data: function () {
    return {
      noimage: noimage,
      shuttleimage: shuttleimage,
      privateimage: privateimage,
      executiveimage: executiveimage,
      dispo: {},
      loader: null,
      transfertype: [false, false, false],
      pricesrange: null,
      language: globallanguage,
      multilanguage: langfile.multilanguage,
      hidefilter: true,
      maxPrice: null,
      minPrice: null
    }
  },
  watch: {
    transfertype: function () {
      this.filterresults()
    }
  },
  methods: {
    resetfilter () {
      var _this = this
      var d = this.$data

      if (d.maxPrice >= 0 && d.minPrice >= 0 && d.maxPrice !== d.minPrice) {
        slider.noUiSlider.reset()
      }

      d.transfertype[0] = false
      d.transfertype[1] = false
      d.transfertype[2] = false
      _this.getAvailability()
    },
    filterresults () {
      var d = this.$data
      d.loader = true
      var prices = [null, null]
      if (d.maxPrice >= 0 && d.minPrice >= 0 && d.maxPrice !== d.minPrice) {
        prices = slider.noUiSlider.get()
      }

      axios({
        method: 'get',
        url: globaldata.baseurl + '/transfer/filter',
        params: {
          from: this.$route.query.origen,
          to: this.$route.query.destino,
          incomingdate: this.$route.query.entrada ? this.$route.query.entrada.replace(/-/g, '') : null,
          outgoingdate: this.$route.query.salida ? this.$route.query.salida.replace(/-/g, '') : null,
          pax: this.$route.query.pax,
          bike: this.$route.query.bike,
          golf: this.$route.query.golf,
          ski: this.$route.query.ski,
          wheelchair: this.$route.query.wheelchair,
          bigLuggages: this.$route.query.bigluggage,
          language: this.$route.query.idioma,
          transfertype: this.formatTransferType(),
          minprice: prices[0],
          maxprice: prices[1],
          localizador: globaldata.localizador
        }
      })
        .then(function (response) {
          d.loader = null
          d.dispo = response.data
          d.viewmap = false
        })
        .catch(function (error) {
          d.loader = null
          console.log(error)
        })
    },
    formatTransferType () {
      var d = this.$data

      var transferTypeString = ''
      if (d.transfertype[0]) {
        transferTypeString = transferTypeString + 'SHUTTLE,'
      }
      if (d.transfertype[1]) {
        transferTypeString = transferTypeString + 'EXECUTIVE,'
      }
      if (d.transfertype[2]) {
        transferTypeString = transferTypeString + 'PRIVATE,'
      }
      if (transferTypeString === '') { return null } else {
        return transferTypeString
      }
    },
    goback (page) {
      this.$router.push({
        name: 'main',
        query: {
          destino: this.$route.query.destino,
          destinoname: this.$route.query.destinoname,
          origen: this.$route.query.origen,
          origenname: this.$route.query.origenname,
          entrada: this.$route.query.entrada,
          salida: this.$route.query.salida,
          idioma: this.$route.query.idioma,
          bike: this.$route.query.bike,
          golf: this.$route.query.golf,
          ski: this.$route.query.ski,
          wheelchair: this.$route.query.wheelchair,
          bigluggage: this.$route.query.bigluggage,
          pax: this.$route.query.pax
        }
      })
    },
    changeParameters () {
      this.$router.push({
        name: 'main',
        query: {
          destino: this.$route.query.destino,
          destinoname: this.$route.query.destinoname,
          origen: this.$route.query.origen,
          origenname: this.$route.query.origenname,
          entrada: this.$route.query.entrada,
          salida: this.$route.query.salida,
          idioma: this.$route.query.idioma,
          bike: this.$route.query.bike,
          golf: this.$route.query.golf,
          ski: this.$route.query.ski,
          wheelchair: this.$route.query.wheelchair,
          bigluggage: this.$route.query.bigluggage,
          pax: this.$route.query.pax
        }
      })
    },
    abrirdetalle (k, description) {
      this.deletetooltips()
      this.$router.push({
        name: 'traslado_detalle',
        query:
            {
              key: k,
              entrada: this.$route.query.entrada,
              salida: this.$route.query.salida,
              destinoname: this.$route.query.destinoname,
              origenname: this.$route.query.origenname,
              transfername: description,
              pax: this.$route.query.pax,
              bike: this.$route.query.bike,
              golf: this.$route.query.golf,
              ski: this.$route.query.ski,
              wheelchair: this.$route.query.wheelchair,
              bigluggage: this.$route.query.bigluggage,
              idorigen: this.$route.query.origen,
              iddestino: this.$route.query.destino,
              origen: this.$route.query.origen,
              destino: this.$route.query.destino
            }
      })
    },
    getDatafromLocator: function () {
      var d = this.$data
      var _this = this

      axios({
        method: 'get',
        url: globaldata.baseurl + '/commons/fromlocator/' + globaldata.localizador,
        params: {}
      })
        .then(function (response) {
          setTimeout(function () {
            _this.$route.query.origen = response.data.origen
            _this.$route.query.origenname = response.data.origenName
            _this.$route.query.destino = response.data.destination
            _this.$route.query.destinoname = response.data.destinationName
            _this.$route.query.entrada = response.data.checkin.toString().substr(0, 4) + '-' + response.data.checkin.toString().substr(4, 2) + '-' + response.data.checkin.toString().substr(6, 2)
            _this.$route.query.salida = response.data.checkout.toString().substr(0, 4) + '-' + response.data.checkout.toString().substr(4, 2) + '-' + response.data.checkout.toString().substr(6, 2)
            _this.$route.query.pax = 0
            _this.$route.query.bike = 0
            _this.$route.query.golf = 0
            _this.$route.query.ski = 0
            _this.$route.query.wheelchair = 0
            _this.$route.query.bigluggage = 0
            _this.$route.query.idioma = d.language
          })
        })
        .catch(function (error) {
          d.loader = null
          console.log(error)
        })
    },

    deletetooltips () {
      var element = document.getElementsByClassName('material-tooltip')
      var index
      for (index = element.length - 1; index >= 0; index--) {
        element[index].parentNode.removeChild(element[index])
      }
    },
    getAvailability: function () {
      var d = this.$data
      var _this = this
      axios({
        method: 'get',
        url: globaldata.baseurl + '/transfer/available',
        params: {
          from: this.$route.query.origen,
          to: this.$route.query.destino,
          incomingdate: this.$route.query.entrada ? this.$route.query.entrada.replace(/-/g, '') : null,
          outgoingdate: this.$route.query.salida ? this.$route.query.salida.replace(/-/g, '') : null,
          pax: this.$route.query.pax,
          bike: this.$route.query.bike,
          golf: this.$route.query.golf,
          ski: this.$route.query.ski,
          wheelchair: this.$route.query.wheelchair,
          bigLuggages: this.$route.query.bigluggage,
          language: this.$route.query.idioma
        }
      })
        .then(function (response) {
          d.loader = false

          d.dispo = response.data

          d.minPrice = d.dispo.minPrice
          d.maxPrice = d.dispo.maxPrice

          setTimeout(function () {
            if (d.maxPrice >= 0 && d.minPrice >= 0 && d.maxPrice !== d.minPrice) {
              var step = 10

              if (((d.maxPrice - d.minPrice) < 50) && ((d.maxPrice - d.minPrice) >= 20)) {
                step = 5
              } else if (d.maxPrice - d.minPrice < 20) {
                step = 1
              }

              slider = document.getElementById('test-slider')
              if (!slider.noUiSlider) {
                noUiSlider.create(slider, {
                  start: [d.minPrice === 0 ? 0 : d.minPrice - 1,
                    d.maxPrice],
                  connect: true,
                  step: step,
                  tooltips: [true, true],
                  pips: {
                    mode: 'count',
                    values: 5,
                    density: 5
                  },
                  orientation: 'horizontal', // 'horizontal' or 'vertical'
                  range: {
                    'min': d.minPrice === 0 ? 0 : d.minPrice - 1,
                    'max': d.maxPrice
                  },
                  format: wNumb({
                    decimals: 0
                  })
                })
                slider.noUiSlider.on('change', function (values, handle) {
                  _this.filterresults()
                })
              }
            }
          })
        })
        .catch(function (error) {
          d.loader = null
          console.log(error)
        })
    }
  },
  mounted () {
    // window.scrollTo(0, 0);

    var d = this.$data
    var _this = this
    d.loader = true
    console.log('locator' + globaldata.localizador)
    if (globaldata.localizador && !this.$route.query.origen) {
      console.log('entra por locator')
      _this.getDatafromLocator()
    } else {
      _this.getAvailability()
    }
  }
}
</script>

